import React, { useEffect } from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import LazyLoad from 'vanilla-lazyload';
import styles from './posts.module.sass';
import Image from '../components/image/image';
import Layout from '../components/layout/layout';
import SEO from '../components/seo/seo';
import { PostsStaticQueryResponse } from '../models/posts.model';

const Posts: React.FC = () => {
  const postsData: PostsStaticQueryResponse = useStaticQuery(graphql`
    query {
      allRestApiPosts(filter: { userId: { eq: 8 } }) {
        edges {
          node {
            userId
            title
            body
            id
          }
        }
      }
    }
  `);

  useEffect(() => {
    const lazyLoadInstance = new LazyLoad({
      elements_selector: '.lazy'
    });
    lazyLoadInstance.update();
  });
  
  return (
    <Layout heading="Gatsby posts">
      <SEO title="Gatsby Posts" />
      <div className={styles.posts}>
        {postsData.allRestApiPosts.edges.map((edge, index) => {
          const { body, title, userId, id } = edge.node;
          return (
            <div className={styles.posts__card} key={index}>
              <h3 className={styles.posts__cardHeader}>{title}</h3>
              <p>Post #{id} by user #{userId}</p>
              <p>
                {body}
              </p>
              <Image 
                imageBase={'/images/blog/background-' + index}
                originalWidth={1024}
                originalHeight={683}
              />
            </div>
          )
        })}
      </div>
    </Layout>
  )
}

export default Posts;
